*{
    box-sizing: border-box;
}
.row:after {
    content: "";
    display: table;
    clear: both;
}

.column{
    text-align: center;
    background-color: #282c34;
    justify-content: center;
    align-items: center;
    display: flex;
    float: left;
    width: 50%;
    padding: 10px;
    height: 75vh;
    color: white;
    flex-direction: column;
}
.column2{
    text-align: center;
    background-color: #282c34;
    justify-content: center;
    align-items: center;
    display: flex;
    float: left;
    width: 100%;
    padding: 10px;
    height: 25vh;
    color: white;
}
.column3{
    text-align: center;
    background-color: #282c34;
    justify-content: center;
    align-items: center;
    display: flex;
    float: left;
    width: 100%;
    padding: 10px;
    height: 75vh;
    color: white;
}
.button {
    border-radius: 5px;
    background-color: #4CAF50; /* Green */
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    -webkit-transition-duration: 0.4s; /* Safari */
    transition-duration: 0.4s;
  }